<template>
	<div class="slider" ref="slider">
		<div class="slider-group" ref="sliderGroup">
			<slot></slot>
		</div>
		<div class="dots">
			<span :class="{active: currentPageIndex === index}" class="dot" v-for="(item,index) in dots"></span>
		</div>
	</div>
</template>

<script type="text/javascript">
import BScroll from 'better-scroll'
import {addClass} from '../../common/js/dom.js'
	export default {
		data(){
			return {
				dots: [],
				currentPageIndex: 0
			}
		},
		props: {
			loop: {
				type: Boolean,
				default: true
			},
			autoPlay: {
				type: Boolean,
				default: true
			},
			interval: {
				type: Number,
				default: 1000
			}
		},
		mounted(){
			setTimeout(() => {
				this._setSliderWidth()
				this._initDots()
				this._initSlider()
				if(this.autoPlay){
					this._play()
				}
			},20)	//浏览器17ms刷新一次

			window.addEventListener('resize', () => {
				if(!this.slider){
					return
				}
				this._setSliderWidth(true)
				this.slider.refresh()                
			})
		},
		methods: {
			_setSliderWidth(isResized){
				this.children = this.$refs.sliderGroup.children

				//图片排成一行宽度之和
				let width = 0

				//一张图片的宽度
				let sliderWidth = this.$refs.slider.clientWidth
				for(let i = 0;i < this.children.length;i++ ){
					let child = this.children[i]

					//已封装的addClass函数
					addClass(child, 'slider-item')

					child.style.width = sliderWidth + 'px'
					width += sliderWidth
				}

				//如果loop=true,则需要增加两个图片的宽度以保证无缝切换
				if( this.loop && !isResized ){
					width += 2 * sliderWidth
				}
				this.$refs.sliderGroup.style.width = width + 'px'
			},
			_initDots(){
				this.dots = new Array(this.children.length)
			},
			_initSlider(){
				this.slider = new BScroll(this.$refs.slider,{
					scrollX: true,
					scrollY: false,
					momentum: false,
					snap: true,
					snapLoop: this.loop,
					snapThreshold: 0.3,
					snapSpeed: 400,
					autoPlay: true
				})

				this.slider.on('scrollEnd', () => {
					let pageIndex = this.slider.getCurrentPage().pageX
					if(this.loop){
						pageIndex -= 1
					}
					this.currentPageIndex = pageIndex
					if(this.autoPlay){
						clearTimeout(this.timer)
						this._play()
					}
				})
			},
			_play(){
				let pageIndex = this.currentPageIndex + 1
				if(this.loop){
					pageIndex += 1
				}
				this.timer = setTimeout(() => {
					this.slider.goToPage(pageIndex,0,400)
				},this.interval)
			}
		},
		destroyed(){
			clearTimeout(this.timer)
		}
	}
</script>

<style scoped lang="stylus">
  @import "~common/stylus/variable"

  .slider
    min-height: 1px
    .slider-group
      position: relative
      overflow: hidden
      white-space: nowrap
      .slider-item
        float: left
        box-sizing: border-box
        overflow: hidden
        text-align: center
        a
          display: block
          width: 100%
          overflow: hidden
          text-decoration: none
        img
          display: block
          width: 100%
    .dots
      position: absolute
      right: 0
      left: 0
      top: 135px
      text-align: center
      font-size: 0
      .dot
        display: inline-block
        margin: 0 4px
        width: 8px
        height: 8px
        border-radius: 50%
        background: $color-text-l
        &.active
          width: 20px
          border-radius: 5px
          background: $color-text-ll
</style>


